import { MaxUint256, AddressZero } from "@ethersproject/constants"
import Bignumber from "bignumber.js"
import gql from "graphql-tag";
import apolloProvider from '@/utils/apollo';
export default {
  components: {
    HeadTop
  },
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      Headtype: '',
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      artContract: null,
      artTokenContract: null,
      usdtToken: null,
      nodeAddress: '0xb019448cbc648069e6c4e4f7965c0b9274126901',
      buyAmount: '0.0000',
      reward: '0.0000',
      tokenPool: '0.0000',
      wethPool: '0.0000',
      luckAmount: '0.0000',
      initContract: () => {
        data.artContract = new data.web3.eth.Contract(ArtAbi, config.ArtAddress)
        data.artTokenContract = new data.web3.eth.Contract(tokenAbi, config.TokenAddress)
        data.routerContract = new data.web3.eth.Contract(swapAbi, config.SwapRouter)
        data.initData()
      },
      initData: () => {
        var clipboard = new Clipboard('.clip-button')
        clipboard.on('success', function (e) {
          Toast(t('share.å¤å¶æå'))
          e.clearSelection()
        })
        clipboard.on('error', function () {
          Toast(t('share.å¤å¶å¤±è´¥ï¼è¯·æå¨é¿æå¤å¶'))
        })
        data.getBuyAndReward()
        data.getReverse()
        data.getLuckAmunt()
      },
      getBuyAndReward: async () => {
        let buyAmount = await data.artContract.methods.userCount(data.address).call()
        let reward = await data.artContract.methods.introRewards(data.address).call()
        data.buyAmount = Number(buyAmount/Math.pow(10, 18)).toFixed(4)
        data.reward = Number(reward/Math.pow(10, 18)).toFixed(4)
      },
      getBuyAmount: async () => {
        let recTime = await data.artContract.methods.openGetTime().call()
        let newdate = parseInt(Date.now()/1000)
        if(Number(newdate) < Number(recTime)){
          Toast(t('receive.æªå°é¢åæ¶é´'))
          return
        }
        // let haveBuy = await data.artContract.methods.haveBuyAmount().call()
        // let total = await data.artContract.methods.totalAmount().call()
        // if(Number(haveBuy) < Number(total)){
        //   Toast('é¢å®æªç»æä¸è½é¢å')
        //   return
        // }
        await data.artContract.methods.withdrawToken().send({from: data.address})
        data.initData()
      },
      getRewardAmount: async () => {
        let recTime = await data.artContract.methods.openGetTime().call()
        let newdate = parseInt(Date.now()/1000)
        if(Number(newdate) < Number(recTime)){
          Toast(t('receive.æªå°é¢åæ¶é´'))
          return
        }
        // let haveBuy = await data.artContract.methods.haveBuyAmount().call()
        // let total = await data.artContract.methods.totalAmount().call()
        // if(Number(haveBuy) < Number(total)){
        //   Toast('é¢å®æªç»æä¸è½é¢å')
        //   return
        // }
        await data.artContract.methods.withdrawReward().send({from: data.address})
        data.initData()
      },
      getReverse: async () => {
        let WETH = await data.routerContract.methods.WETH().call()
        let factory = new data.web3.eth.Contract(factoryAbi, config.FactoryAddress)
        let pair = await factory.methods.getPair(WETH, config.TokenAddress).call()
        let pairContract = new data.web3.eth.Contract(pairAbi, pair)
        let token0 = await pairContract.methods.token0().call()
        let reserves = await pairContract.methods.getReserves().call()
        console.log(reserves)
        if(token0.toLowerCase() == WETH.toLowerCase()) {
          data.tokenPool = Number(reserves._reserve1/Math.pow(10, 18)).toFixed(4)
          data.wethPool = Number(reserves._reserve0/Math.pow(10, 18)).toFixed(4)
        } else {
          data.tokenPool = Number(reserves._reserve0/Math.pow(10, 18)).toFixed(4)
          data.wethPool = Number(reserves._reserve1/Math.pow(10, 18)).toFixed(4)
        }
      },
      getLuckAmunt: () => {
        let _this = this
        apolloProvider.clients.tokenClient.query({
        // query: gql`${this.sqlName}`,
        // query ascListing($id: String!) {  listing: ascListing(id: $id) {    channels    id    agencyPropertyNumber    isSaleableAreaVerified    shareLink    title    formattedTitle {      actual      __typename    }    subtitle    tier    propertyType    prices {      type      currency      label      symbol      max      min      minPricePerSizeUnitByBuiltUp      maxPricePerSizeUnitByBuiltUp      __typename    }    cover {      ...mediaFields      __typename    }    medias {      ...mediaFields      __typename    }    youtubeIds    floorPlanImages {      ...mediaFields      __typename    }    description    updatedAt    publishedAt    address {      locality      formattedAddress      lat      lng      hideMarker      __typename    }    unitFeatures {      code      description {        enGB        zhHK        zhCN        msMY        idID        __typename      }      __typename    }    buildingFacilities {      code      description {        enGB        zhHK        zhCN        msMY        idID        __typename      }      __typename    }    featureDescription    referenceCode    transacted    multilanguagePlace {      enGB {        level1        level2        level3        __typename      }      __typename    }    attributes {      auctionDate      bathroom      bedroom      landArea      carPark      builtUp      furnishing      floorZone      buildingAge      maintenanceFee      layout      landTitleType      tenure      availableDate      aircond      pricePSF      pricePerSizeUnit      minimumPricePerSizeUnit      maximumPricePerSizeUnit      facingDirection      unitType      occupancy      titleType      promotion      highlight      sizeUnit      sizeUnitLandArea      bumiDiscount      threeDUrl      image360      propertyCondition      maidRooms      maidBathroom      floors      electricity      certificate      phoneLine      hideBuildingName      unexpiredLeaseYear      landAreaDimension      builtUpDimension      pricePerSizeUnitBuiltUp      pricePerSizeUnitLandArea      storeys      unit      floors      block      buildingId      township      isBumiLot      __typename    }    listers {      id      type      name      license      image {        ...mediaFields        __typename      }      website      contact {        phones {          label          number          via          __typename        }        __typename      }      createdAt      __typename    }    organisations {      id      type      name      email      license      website      description      color      logo {        ...mediaFields        __typename      }      address {        formattedAddress        __typename      }      contact {        phones {          number          label          __typename        }        emails        __typename      }      __typename    }    __typename  }}fragment mediaFields on Media {  url  urlTemplate  thumbnailUrl  __typename}
        query: gql`query  luckyUsers($first: Int!, $id: ID!) {
          luckyUsers(first: $first, where: {id: $id}) {
            id
            amount
          }
        }`,
        // variables:params,
      // variables: {
      //   category:"lifestyle"
      // },
     variables: {first: 1, id: data.address.toLowerCase()},
        fetchPolicy: 'no-cache'
      }).then((result) => {
        if(result.data.luckyUsers.length) {
          data.luckAmount = Number(result.data.luckyUsers[0].amount/Math.pow(10, 18)).toFixed(4)
        }
        // data.list = result.data.introIncomeLists
      }).catch((err, data) => {
        console.log(err, data)
        
      })
      }
    })
    if (data.address) {
